<template>

<h3 class="title" style="font-weight:900;">  Q&A </h3><hr>

<div v-if="state.ismember==false">
    <div class="alert alert-success" role="alert">
      Q/A 써클 가입 후 이용가능합니다.
    </div>
</div>

  <div class="container-lg" v-if="state.ismember==true" style="overflow-y: scroll;height: 600px;">
    <!-- Circle 멤버일 경우에만 questiolist open -->
    <h1  hidden >{{state.ismember}}</h1>
    <div class="regist">
      <el-button class="regist-btn" type="primary" plain @click="state.showModal=true"> <span> 질문 등록하기 </span>
      </el-button>
    </div>

    <div class="accordion" id="accordionPanelsStayExample">
      <!-- Question -->
      <div class="accordion-item question" v-for="(question, i) in state.lists" :key="i">
        <div hidden> CircleId that I Joined :: {{question.circleid}} </div>
        <h2 class="accordion-header" v-bind:id="'panelsStayOpen-heading'+i">
          <button class="accordion-button collapsed" type="button" data-bs-toggle="collapse"
            v-bind:data-bs-target="'#panelsStayOpen-collapse'+i" aria-expanded="true"
            v-bind:aria-controls="'panelsStayOpen-collapse'+i">
            <div ref="questionid" hidden>{{question.questionid}}</div>
            <div class="titlecontent">{{ i+1 }}. {{question.title}} </div>
            <!-- <div v-else>no Question</div>  v-if="question.title != ''" -->
          </button>
        </h2>

        <div v-bind:id="'panelsStayOpen-collapse'+i" class="accordion-collapse collapse"
          v-bind:aria-labelledby="'panelsStayOpen-heading'+i">

          <div class="buttongoup">
            <el-button-group class="ml-4" v-if="question.uid == state.registCheckuid">
              <el-button class="titlebtn" type="primary" plain data-bs-toggle="modal" data-bs-target="#staticBackdrop1"
                @click="sendId(question.questionid, question.nickname, question.title, question.content)"> <span>
                  질문 수정하기 </span></el-button>
              <el-button class="titlebtn" type="primary" plain @click="open(), sendId(question.questionid, question.nickname, question.title, question.content)"> <span> 삭제 </span> </el-button>
            </el-button-group>
          </div>

          <!-- <div class="switch">
          <label class="publiclabel"> 공개여부 ::  </label>
          <el-switch  v-model="state.questionPublic" active-text="public" inactive-text="private"  disabled />
        </div> -->

          <div class="accordion-body">
            <p hidden> {{ state.uid=question.uid }} </p>
            <p><b>작성자 :: </b> {{ question.nickname }} </p>
            <p><b>질문 내용 :: </b>{{ question.content }} </p>
            <p><b>수정 일자 :: </b>{{formatDate(question.modDate)}} </p>
          </div>

          <!-- Answer -->
          <div class="answerList">
            <AnswerByCircle 
            :questionid="question.questionid" 
            :qemail="state.email"
            :registCheckuid="state.registCheckuid" />
          </div>

        </div>

      </div>
      <!--accordion-item question-->
    </div>
    <!--arccodian-->

    <!-- Modal -->
    <div class="modal fade" id="staticBackdrop1" data-bs-backdrop="static" data-bs-keyboard="false" tabindex="-1"
      aria-labelledby="staticBackdropLabel" aria-hidden="true">
      <div class="modal-dialog">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title" id="staticBackdropLabel">질문 수정하기</h5>
            <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
          </div>

          <div class="modal-body container">

            <!-- <div class="item modalswitch">
              <label for="nickname"> 공개여부 :: </label><br>
              <el-switch v-model="questionPublic" size="large" active-text="Public" />
            </div>

            <div class="item">
              <label for="nickname"> 이메일 :: </label><br>
              <input type="text" id="nickname" v-model="modal.nickname" disabled>
            </div> -->

            <div class="item">
              <label for="title"> 제목 :: </label><br>
              <input type="text" id="title" v-model="modal.title">
            </div>

            <div class="item">
              <label for="content"> 질문 내용 :: </label><br>
              <textarea type="text" id="content" v-model="modal.content"></textarea>
            </div>
          </div>

          <div class="modal-footer">
            <button type="button" class="btn btn-secondary" data-bs-dismiss="modal"> 닫기 </button>
            <button type="button" class="btn btn-primary" @click="modifyQuestion()"
              data-bs-dismiss="modal">수정하기</button>
          </div>
        </div>
      </div>
    </div>

    <!-- Register -->
    <Teleport to="body">
      <!-- use the modal component, pass in the prop -->
      <modal :show="state.showModal" @close="state.showModal=false">
        <template #header>
          <h3>질문 등록하기</h3>
        </template>
        <template #body>

          <!-- <div class="item modalswitch">
            <label for=""> 공개여부 :: </label><br>
            <el-switch size="large" active-text="Public" v-model="questionPublic" />
          </div>

          <div class="item">
            <label for=""> 이메일 :: </label><br>
            <input type="text" v-model="state.email" disabled>
          </div> -->

          <div class="item">
            <label for="title"> 질문 제목 :: </label><br>
            <input type="text" id="title" v-model="regist.registTitle" style="width:400px;">
          </div>

          <div class="item">
            <label for="content"> 질문 내용 :: </label><br>
            <textarea type="text" id="content" v-model="regist.registContent"></textarea>
          </div>

        </template>
        <template #footer>
          <el-button type="secondary" plain @click="state.showModal=false"> 닫기 </el-button>
          <el-button type="primary" plain @click="questionRegister()"> 등록하기 </el-button>
        </template>
      </modal>
    </Teleport>

  </div>

</template>


<script>
import { ElMessage, ElMessageBox } from 'element-plus'
import { onMounted, reactive, ref, toRef } from 'vue'
import Modal from './teleport/Modal.vue'
import {useRouter} from 'vue-router';
import AnswerByCircle from './AnswerByCircle.vue'
import axios from 'axios'
import dayjs from 'dayjs'

export default {
  name: "TheQuestion",
  components: { AnswerByCircle, Modal },
  props: ['circleid', 'ismember'],
  setup(props) {
    onMounted(() => { loginHandler(), registerUid() })
   
    const router = useRouter()
    const questionPublic = ref(true)
    const circleid = toRef(props, 'circleid')
    const ismember = toRef(props, 'ismember')
    const state = reactive({
      qid: '',
      registCheckuid: '',
      uid: '',
      circleid: circleid,
      nickname: '',
      questionPublic: '',
      lists: '',
      modiDate: '',
      ismember: ismember,
      email: sessionStorage.getItem('email'),
      token: sessionStorage.getItem('TOKEN'),
    });

    const formatDate = (dateString) => {
      const date = dayjs(dateString);
      return date.format('YYYY-MM-D HH:mm');
    };
    const modal = reactive({
      questionid: '',
      nickname: '',
      title: '',
      content: ''
    })
    const sendId = (questionid, nickname, title, content) => {
      modal.questionid = questionid
      modal.nickname = nickname
      modal.title = title
      modal.content = content
    };
    //delete Modal
    const open = () => {
      ElMessageBox.confirm(
        '질문을 수정하시겠습니까?',
        'Warning',
        {
          confirmButtonText: '예',
          cancelButtonText: '취소',
          type: 'warning',
        }
      )
        .then(() => {
          ElMessage({
            type: 'success',
            message: '삭제완료되었습니다.',
          })
          deleteQuestion()
        })
        .catch(() => {
          ElMessage({
            type: 'info',
            message: '취소되었습니다.',
          })
        })
    }
    const loginHandler = async () => {
      const url = "http://localhost:9090/api/questionlistincircle"

      const headers = {
        //TODO send the right amound of tokent and receive the same...
        'Content-Type': 'application/json',
        Authorization: state.token,
        token: state.token,
        'name': 'test list'
      }
      const body = { circleid: state.circleid }
      await axios.post(url, body, { headers }).then(function (res) {
        if (res.status == 200) {
          state.lists = res.data
        } else {
          alert('bad.')
        }
      })
    }
    const registerUid = async () => {   // 글 등록시 로그인한 사용자가 맞는지 확인용
      const url = "http://localhost:9090/api/questionregiuid"
      const headers = {
        //TODO send the right amound of tokent and receive the same...
        'Content-Type': 'application/json',
        Authorization: state.token,
        token: state.token,
        'name': 'test list'
      }
      const body = { email: state.email }
      await axios.post(url, body, { headers }).then(function (res) {
        if (res.status == 200) {
          state.registCheckuid = res.data
        } else {
          alert('bad.')
        }
      })
    }
    const deleteQuestion = async () => {
      const url = 'http://localhost:9090/api/deletequestionincircle'
      const headers = {
        "Content-Type": "application/json",
        Authorization: state.token,
        "token": state.token
      }
      const body = { questionid: modal.questionid, }
      await axios.post(url, body, { headers }).then(function (res) {
        if (res.status === 200) {
          router.go(0);
        }
      })
    }
    const modifyQuestion = async () => {
      if (state.title === '') {
        alert("Please input title"); title.value.focus(); return false;
      }
      if (state.content === '') {
        alert("Please input content"); content.value.focus(); return false;
      }
      const url = "http://localhost:9090/api/questionModify"
      const headers = {
        "Content-Type": "application/json",
        Authorization: state.token,
        "token": state.token,
      }
      const body = {
        questionid: modal.questionid,
        nickname: modal.nickname,
        title: modal.title,
        content: modal.content,
        questionPublic: questionPublic.value,
        modDate: state.modDate
      }
      const response = await axios.post(url, body, { headers })
      if (response.status === 200) {
        alert('수정이 완료되었습니다.')
        setTimeout(loginHandler(), 1000);
      } else {
        alert('fail')
      }
    }
    const regist = reactive({
      registContent: '',
      registTitle: ''
    });
    const questionRegister = async () => {
      if (regist.registContent === '') {
        alert('Please Input your content'); email.value.focus(); return false;
      }
      const url = 'http://localhost:9090/api/questionRegistercircle'
      const headers = {
        "Content-Type": "application/json",
        Authorization: state.token,
        "token": state.token
      }
      const body = {
        email: state.email,
        questionid: state.qid,
        uid: state.registCheckuid,
        circleid: state.circleid,
        title: regist.registTitle,
        content: regist.registContent,
        questionPublic: questionPublic.value,
      }
      const response = await axios.post(url, body, { headers })
      if (response.status === 200) {
        alert('등록이 완료되었습니다.');
        state.showModal = false
        router.go(0);
      } else {
        alert('fail')
      }
    }

    return {
      questionPublic, sendId, modal, formatDate,
      open, modifyQuestion, state, loginHandler, deleteQuestion,
      regist, questionRegister, registerUid
    }

  }

}


</script>

<style scoped>
.regist {
  display: block;
  text-align: right;
  margin: 10px;
}

.regist-btn {
  color: rgb(6, 90, 92);
  background: rgba(6, 90, 92, 0.1);
  border: none;
}

.regist-btn:hover {
  color: rgb(6, 90, 92);
  background: rgba(6, 90, 92, 0.1);
  box-shadow: 0px 6px 10px rgba(6, 90, 92, 0.25);
  transition: all 0.2s;

}

.accordion-button {
  display: flex;
  justify-content: space-between;
}

.accordion-button {
  background: rgba(6, 90, 92, 0.2);
  color: rgba(0, 0, 0, 0.8)
}

.accordion-button:hover {
  box-shadow: 0px 6px 10px rgba(0, 0, 0, 0.4);
  transform: scale(1.01);
}

.accordion-button:focus {
  box-shadow: 0px 6px 10px rgba(6, 90, 92, 0.25);
  transition: all 0.2s;
}

.titlecontent{
  display: block;
  width: 350px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  margin: 0;
}

.accordion-body {
  display: block;
  text-align: left;
}

.buttongoup {
  display: block;
  margin: 10px;
  margin-top: 10px;
  text-align: right;
}

.buttongoup span {
  font-size: 15px;
}

.titlebtn {
  color: rgba(6, 90, 92, 0.9);
  border: 1px solid rgba(6, 90, 92, 0.5);
  background: rgba(6, 90, 92, 0.02);
}

.titlebtn:hover {
  background: rgba(25, 135, 84, 0.1);
}


.answer_collapse {
  border: solid 1px grey;
  padding: 0 10px;
  margin: 10px 0;
}

.answer {
  display: flex;
}

.counter {
  display: flex;
  align-items: center;
  height: 80px;
  margin-right: 30px;
  background-color: lightgray;
  padding: 0 10px;
  border-radius: 30%;
}

.num {
  font-size: 30px;
  color: rgb(6, 90, 92);
  font-weight: bold;
  margin: 10px;
}

button {
  font-size: 20px;
  font-weight: bold;
}

.switch {
  display: block;
  text-align: right;
  margin-right: 10px;
}

.publiclabel {
  margin: 10px;
}

.container {
  display: block;
  text-align: left;
  width: 500px;
  height: 330px;
}

label {
  margin: 10px 0;
}

textarea {
  width: 400px;
  height: 180px;
}
</style>