<template>
  <div class="btnreg">
    <el-button type="info" plain @click="state.showModal=true"> <span>답변 등록하기</span> </el-button>
  </div>

  <div class="accordion" id="accordionPanelsStayOpenExample">
    <!-- Answer -->
    <div class="accordion-item " v-for="(answer, i) in state.lists" :key="i">

      <h2 class="accordion-header" v-bind:id="'panelsStayOpen-heading'+answer.answerid">
        <h4 hidden> {{state.aid = answer.answerid}}</h4>
        <button class="accordion-button collapsed" type="button" data-bs-toggle="collapse"
          v-bind:data-bs-target="'#panelsStayOpen-collapse'+answer.answerid" aria-expanded="true"
          v-bind:aria-controls="'panelsStayOpen-collapse'+answer.answerid">
          <div class="titlecontent" v-if="answer.content != ''"> ┗ &nbsp; {{ answer.content }}</div>
          <div class="titlecontent" v-else> ┗ &nbsp; No answer</div>
        </button>
      </h2>

      <div v-bind:id="'panelsStayOpen-collapse'+answer.answerid" class="accordion-collapse collapse"
        v-bind:aria-labelledby="'panelsStayOpen-heading'+answer.answerid">

        <div class="buttongoup">
          <el-button-group class="ml-4" v-if="answer.uid == state.registCheckuid">
            <el-button type="info" plain data-bs-toggle="modal" data-bs-target="#staticBackdrop2"
              @click="sendId(answer.answerid, answer.nickname, answer.content, answer.rate)"> <span> 답변 수정
              </span></el-button>
            <el-button type="info" plain @click="deleteModal"> <span> 삭제 </span> </el-button>
          </el-button-group>
        </div>

        <div class="accordion-body">
          <!-- rate answer -->
          <div class="counter">
            <div class="buttons">
              <button
                v-on:click.once="answer.rate >= 0 ? answer.rate+=1 : answer.rate+=0, sendId(answer.answerid, answer.nickname, answer.content, answer.rate), modifyAnswer()">
                &#708; </button>
              <button
                v-on:click.once="answer.rate != 0 ? answer.rate-=1 : answer.rate-=0, sendId(answer.answerid, answer.nickname, answer.content, answer.rate), modifyAnswer()">
                &#709; </button>
            </div>
            <div class="num" ref="num">
              {{ answer.rate }}
            </div>
          </div>

          <!-- answer content -->
          <div class="container">

            <!-- <div class="switch">
              <label class="publiclabel"> 공개여부 </label>
              <h2 hidden>{{state.answerPublic = answer.answerPublic}}</h2>
              <el-switch  v-model="state.answerPublic" active-text="public" inactive-text=""  />
            </div> -->

            <div class="content">
              <p> <b> 작성자 :: </b> {{ answer.nickname }}</p>
              <p> <b> 답변 내용 :: </b> {{ answer.content }} </p>
              <p> <b> 수정 일자 :: </b> {{formatDate(answer.modDate)}}</p>
              <p hidden> {{state.modDate = answer.modDate}}</p>
            </div>
          </div>

        </div>


        <!-- Comment -->
        <div class="commentList">
          <CommentByCircle :answerid="answer.answerid" :qemail="state.qemail" :registCheckuid="state.registCheckuid" />
        </div>

      </div>

    </div>
    <!--accordion-item-->
  </div>
  <!--arccodian-->

  <!-- Modify Modal -->
  <div class="modal fade" id="staticBackdrop2" data-bs-backdrop="static" data-bs-keyboard="false" tabindex="-1"
    aria-labelledby="staticBackdropLabel" aria-hidden="true">
    <div class="modal-dialog">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="staticBackdropLabel">답변 수정하기</h5>
          <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
        </div>

        <div class="modal-body modalcontainer">

          <!-- <div class="item modalswitch">
            <label for=""> 공개여부 :: </label><br>
            <el-switch v-model="answerPublic" size="large" active-text="Public" />
          </div>

          <div class="item">
            <label for=""> 닉네임 :: </label><br>
            <input type="text" v-model="modal.nickname" disabled>
          </div> -->

          <div class="item">
            <label for="content"> 답변내용 :: </label><br>
            <textarea type="text" id="content" v-model="modal.content"></textarea>
          </div>
        </div>

        <div class="modal-footer">
          <button type="button" class="btn btn-secondary" data-bs-dismiss="modal">닫기</button>
          <button type="button" class="btn btn-primary" @click="modifyAnswer()" data-bs-dismiss="modal">수정하기</button>
        </div>
      </div>
    </div>
  </div>

  <!-- Register -->
  <Teleport to="body">
    <!-- use the modal component, pass in the prop -->
    <modal :show="state.showModal" @close="state.showModal=false">
      <template #header>
        <h3>답변 등록하기</h3>
      </template>
      <template #body>

        <!-- <div class="item modalswitch">
          <label for="nickname"> 공개여부 :: </label><br>
          <el-switch size="large" active-text="Public" v-model="answerPublic" />
        </div>

        <div class="item">
          <label for="nickname"> 닉네임 :: </label><br>
          <input type="text" id="nickname" v-model="state.qemail" disabled>
        </div> -->

        <div class="item">
          <label for="content"> 답변 내용 :: </label><br>
          <textarea type="text" id="content" v-model="regist.registContent"></textarea>
        </div>

      </template>
      <template #footer>
        <el-button type="secondary" plain @click="state.showModal=false"> 닫기 </el-button>
        <el-button type="primary" plain @click="answerRegister()">등록하기</el-button>
      </template>
    </modal>
  </Teleport>



</template>

<script>
import { ElMessage, ElMessageBox } from 'element-plus'
import { onMounted, toRef, ref } from 'vue'
import { reactive, } from 'vue'
import { useRouter } from 'vue-router';
import Modal from './teleport/Modal.vue'
import axios from 'axios'
import dayjs from 'dayjs'
import CommentByCircle from './CommentByCircle.vue'

export default {
  name: "TheAnswer",
  components: { CommentByCircle, Modal },
  props: ['questionid', 'qemail', 'registCheckuid'],

  setup(props) {

    onMounted(() => { answerlistByCircle() })

    const router = useRouter()
    const qid = toRef(props, 'questionid')
    const qemail = toRef(props, 'qemail')
    const registCheckuid = toRef(props, 'registCheckuid')
    const answerPublic = ref(true)

    const formatDate = (dateString) => {
      const date = dayjs(dateString);
      return date.format('YYYY-MM-D HH:mm');
    };

    const state = reactive({
      qid: qid,  //circleid로 검색된 질문id
      registCheckuid: registCheckuid, //로그인한 유저의 id
      qemail: qemail,
      aid: '',
      answerPublic: '',
      modDate: '',
      lists: '',
      email: sessionStorage.getItem('email'),
      token: sessionStorage.getItem('TOKEN'),
      showModal: false,
    });

    const modal = reactive({
      answerid: '',
      nickname: '',
      content: '',
      rate: '',
    })

    const sendId = (answerid, nickname, content, rate) => {
      modal.answerid = answerid
      modal.nickname = nickname
      modal.content = content
      modal.rate = rate
    };

    const deleteModal = () => {
      ElMessageBox.confirm(
        '답변을 삭제하시겠습니까?',
        '알림',
        {
          confirmButtonText: '예',
          cancelButtonText: '아니오',
          type: 'warning',
        }
      )
        .then(() => {
          ElMessage({
            type: 'success',
            message: '삭제되었습니다.',
          })
          deleteAnswer()
        })
        .catch(() => {
          ElMessage({
            type: 'info',
            message: '취소되었습니다.',
          })
        })
    }

    const answerlistByCircle = async () => {
      const url = "http://localhost:9090/api/answerlistbycircle"

      const headers = {
        //TODO send the right amound of tokent and receive the same...
        'Content-Type': 'application/json',
        Authorization: state.token,
        token: state.token,
        'name': 'test list'
      }
      const body = { questionid: state.qid }
      await axios.post(url, body, { headers }).then(function (res) {
        if (res.status == 200) {
          state.lists = res.data
        } else {
          alert('bad.')
        }
      })
    }

    const deleteAnswer = async () => {
      const url = 'http://localhost:9090/api/deleteAnswer'
      const headers = {
        "Content-Type": "application/json",
        Authorization: state.token,
        "token": state.token
      }
      const body = { answerid: state.aid, }
      await axios.post(url, body, { headers }).then(function (res) {
        if (res.status === 200) {
          setTimeout(answerlistByCircle(), 1000);
        }
      })
    }

    const modifyAnswer = async () => {
      if (modal.content === '') {
        alert("내용을 적어주세요"); content.value.focus(); return false;
      }
      const url = "http://localhost:9090/api/modifyanswerincircle"
      const headers = {
        "Content-Type": "application/json",
        Authorization: state.token,
        "token": state.token,
      }
      const body = {
        answerid: modal.answerid,
        answerPublic: answerPublic.value,
        nickname: modal.nickname,
        content: modal.content,
        modDate: state.modDate,
        rate: modal.rate,
      }
      const response = await axios.post(url, body, { headers })
      if (response.status === 200) {
        setTimeout(answerlistByCircle(), 1000);
      } else {
        alert('fail')
      }
    }

    const regist = reactive({
      registContent: '',
    });


    const answerRegister = async () => {
      if (regist.registContent === '') {
        alert('내용을 적어주세요'); email.value.focus(); return false;
      }
      const url = 'http://localhost:9090/api/answerRegister'
      const headers = {
        "Content-Type": "application/json",
        Authorization: state.token,
        "token": state.token
      }
      const body = {
        questionid: state.qid,
        uid: state.registCheckuid,
        answerPublic: answerPublic.value,
        content: regist.registContent,
      }
      const response = await axios.post(url, body, { headers })
      if (response.status === 200) {
        alert('답변이 등록되었습니다.');
        state.showModal = false
        setTimeout(answerlistByCircle(), 1000);
      } else {
        alert('fail')
      }
    }

    return { formatDate, answerPublic, deleteModal, state, answerlistByCircle, deleteAnswer, modifyAnswer, modal, sendId, regist, answerRegister }

  }

};

</script>

<style scoped>
.btnreg {
  display: block;
  text-align: right;
  margin: 10px;
}

button {
  font-size: 20px;
  font-weight: bold;
}

.accordion-button {
  background: rgba(6, 90, 92, 0.07);
  color: rgba(0, 0, 0, 0.8)
}

.accordion-button:hover {
  box-shadow: 0px 6px 10px rgba(0, 0, 0, 0.4);
  transform: scale(1.01);
}

.accordion-button:focus {
  box-shadow: 0px 6px 10px rgba(6, 90, 92, 0.25);
  transition: all 0.2s;
}

.titlecontent {
  display: block;
  width: 350px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  margin: 0;
}

.buttongoup {
  margin: 10px;
}

.accordion-body {
  display: flex;
}

.counter {
  display: flex;
  align-items: center;
  height: 80px;
  margin-right: 30px;
  background-color: lightgray;
  padding: 0 10px;
  border-radius: 30%;
}

.num {
  font-size: 30px;
  color: rgb(6, 90, 92);
  font-weight: bold;
  margin: 10px;
}

.buttongoup {
  display: block;
  text-align: right;
}

span {
  font-size: 15px;
}

.container {
  display: block;
  text-align: left;
}

.switch {
  display: block;
  text-align: right;
}

.buttons button {
  font-size: 15px;
  display: block;
  margin: 0 auto;
  height: 10x;
  border: none;
  border-radius: 10px;
}

.modalcontainer {
  display: block;
  text-align: left;
  width: 500px;
  height: 100%px;
}

.publiclabel {
  margin: 10px;
}

label {
  margin: 10px 0;
}

textarea {
  width: 400px;
  height: 180px;
}
</style>>

