<template>
  <div class="btnreg">
    <el-button plain @click="state.showModal=true"> 댓글 등록하기 </el-button>
  </div>

  <div class="card" v-for="(comment, i) in state.lists" :key="i">

    <div class="buttongoup"  v-if="state.registCheckuid == comment.uid">
      <el-button-group class="ml-4">
        <el-button plain @click="open"> 삭제 </el-button>
      </el-button-group>
    </div>

    <div class="card-body container" v-if="comment.uid != null">
      <div class="item1"> <b> 댓글 작성자 </b> {{comment.nickname}} </div>
      <div class="item2">
        <p> <b>댓글 내용:: </b> {{comment.content}} </p>
        <p> <b>수정 일자:: </b> {{formatDate(comment.modDate)}} </p>
        <p hidden> {{state.cid=comment.commentid}} </p>
      </div>
    </div>


  </div>



  <!-- Register -->
  <Teleport to="body">
    <!-- use the modal component, pass in the prop -->
    <modal :show="state.showModal" @close="state.showModal=false">
      <template #header>
        <h3>댓글 등록</h3>
      </template>
      <template #body>

        <!-- <div class="item">
          <label for="email"> 이메일 :: </label><br>
          <input type="text" id="email" v-model="state.qemail" disabled>
        </div> -->
        <div class="item">
          <label for="content"> 댓글 내용 :: </label><br>
          <textarea type="text" id="content" v-model="regist.registContent"></textarea>
        </div>

      </template>
      <template #footer>
        <el-button type="secondary" plain @click="state.showModal=false"> 닫기 </el-button>
        <el-button type="primary" plain @click="commentRegister()">등록하기</el-button>
      </template>
    </modal>
  </Teleport>


</template>

<script>
import { ElMessage, ElMessageBox } from 'element-plus'
import { onMounted, toRef, ref } from 'vue'
import { reactive, } from 'vue'
import { useRouter } from 'vue-router';
import Modal from './teleport/Modal.vue'
import dayjs from 'dayjs'
import axios from 'axios'

export default {
  name: "TheComment",
  props: ['answerid', 'registCheckuid', 'qemail'],
  components: { Modal, },


  setup(props) {

    onMounted(() => { displayComment() })

    const router = useRouter()
    const aid = toRef(props, 'answerid')
    const registCheckuid = toRef(props, 'registCheckuid')
    const qemail = toRef(props, 'qemail')

    const state = reactive({
      aid: aid,
      qemail: qemail,
      registCheckuid: registCheckuid,
      cid: '',
      content: '',
      lists: '',
      modDate: '',
      email: sessionStorage.getItem('email'),
      token: sessionStorage.getItem('TOKEN'),
      showModal: false,
    });


    const formatDate = (dateString) => {
      const date = dayjs(dateString);
      return date.format('YYYY-MM-D HH:mm');
    };
    const open = () => {
      ElMessageBox.confirm(
        '댓글을 삭제하시겠습니까?',
        '알림',
        {
          confirmButtonText: '예',
          cancelButtonText: '아니오',
          type: 'warning',
        }
      )
        .then(() => {
          ElMessage({
            type: 'success',
            message: '삭제되었습니다.',
          })
          deleteComment()
        })
        .catch(() => {
          ElMessage({
            type: 'info',
            message: '취소되었습니다.',
          })
        })
    }
    const displayComment = async () => {
      const url = "http://localhost:9090/api/commentbyanswer"

      const headers = {
        //TODO send the right amound of tokent and receive the same...
        'Content-Type': 'application/json',
        Authorization: state.token,
        token: state.token,
        'name': 'test list'
      }
      const body = { answer: state.aid }
      await axios.post(url, body, { headers }).then(function (res) {
        state.lists = res.data

      }).catch((err) => { console.log('err' + err) })
    }

    const deleteComment = async () => {
      const url = 'http://localhost:9090/api/deleteCommentincircle'
      const headers = {
        "Content-Type": "application/json",
        Authorization: state.token,
        "token": state.token
      }
      const body = { commentid: state.cid, }
      await axios.post(url, body, { headers }).then(function (res) {
        if (res.status === 200) {
          setTimeout(displayComment(), 1000);
        }
      })
    }

    const regist = reactive({
      registContent: '',
    });

    const commentRegister = async () => {
      if (regist.registContent === '') {
        alert('Please Input your content'); email.value.focus(); return false;
      }
      const url = 'http://localhost:9090/api/commentRegister'
      const headers = {
        "Content-Type": "application/json",
        Authorization: state.token,
        "token": state.token
      }
      const body = {
        answerid: state.aid,
        uid: state.registCheckuid,
        content: regist.registContent,
      }
      const response = await axios.post(url, body, { headers }).then(function (res) {
        alert('댓글등록이 왼료되었습니다');
        displayComment()
        state.showModal = false;
      }).catch((err) => {
        alert('fail')
      })
    }
    return { formatDate, open, state, displayComment, deleteComment, regist, commentRegister }
  }


};

</script>

<style scoped>
.container {
  display: grid;
  grid-template-columns: 1fr 3fr;
}

.btnreg {
  display: block;
  text-align: right;
  margin: 10px;
}

.buttongoup {
  display: block;
  margin: 10px;
  margin-top: 10px;
  text-align: right;
}

.item1 {
  border-right: 1px solid gray;
  margin-right: 10px;
  padding-right: 10px;
}

.item2 {
  display: block;
  text-align: left;
}

.modalcontainer {
  display: block;
  text-align: left;
  width: 500px;
  height: 100%px;
}

label {
  margin: 10px 0;
}

textarea {
  width: 400px;
  height: 100px;
}
</style>
